@import url(../../fonts/fonts.css);
.paddings {
  padding-left: 20px;
  padding-right: 20px;
}
.url {
  display: block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'league_gothic_regular', sans-serif;
}
body {
  width: 100%;
  height: 100%;
  max-height: 800px;
  font-family: 'open_sans_light', Tahoma, sans-serif;
  font-size: 16px;
  line-height: 24px;
  background-image: url(../../img/body-bg.jpg);
  background-position-x: center;
  background-repeat: no-repeat;
}
main.container-fluid {
  padding-left: 0;
  padding-right: 0;
}
.row {
  margin-right: 0px;
  margin-left: 0px;
}
h1,
h2,
h3 {
  font-family: 'league_gothic_regular', sans-serif;
}
header {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 60px;
  margin-bottom: 75px;
}
header .logo {
  display: block;
  font-size: 0px;
  width: 155px;
  height: 39px;
  margin-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
}
header .logo img {
  width: 155px;
  height: 39px;
}
.main-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
}
.main-nav ul li a {
  margin-right: 20px;
  display: block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'league_gothic_regular', sans-serif;
  font-size: 24px;
}
.main-nav ul li a:hover,
.main-nav ul li a .active {
  border-bottom: 2px solid #edbfff;
}
.slider .wrapper-slider {
  margin-top: 30px;
  margin-bottom: 100px;
  height: 200px;
}
.slider h1,
.slider h2 {
  text-align: center;
  line-height: 24px;
}
.slider h1 {
  font-size: 36px;
  color: white;
}
.slider h2 {
  font-size: 18px;
  color: #edbfff;
}
.about {
  width: 100%;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 75px;
  padding-bottom: 60px;
  background-image: linear-gradient(to top, #322539 0%, #745083 100%);
  color: white;
}
.about article {
  padding-left: 51px;
  background-image: url(../../img/about-sprite.png);
  background-repeat: no-repeat;
  margin-bottom: 50px;
}
.about article a {
  display: block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'league_gothic_regular', sans-serif;
  float: right;
}
.about article a:hover {
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.8);
}
.content {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 90px;
  padding-bottom: 30px;
  background-color: #f9f9f9;
  box-shadow: inset 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
}
.content .review {
  background-color: white;
  padding: 35px;
  position: relative;
  margin-bottom: 60px;
}
.content .review:before {
  content: '';
  display: block;
  position: absolute;
  width: 49px;
  height: 49px;
  background-image: url(../../img/review-img.png);
  top: 34px;
  left: -19px;
}
.content .review .title {
  font-family: 'league_gothic_regular', sans-serif;
  font-size: 24px;
  text-transform: uppercase;
}
.content .review p:first-child {
  color: black;
  margin-bottom: 0px;
}
.content .review p:nth-child(2) {
  color: #edbfff;
}
.content .articles {
  padding-left: 0px;
  padding-right: 0px;
}
.content .articles article {
  padding-left: 55px;
  background-image: url(../../img/articles-img.png);
  background-repeat: no-repeat;
}
.content .articles h4 {
  font-family: 'open_sans_extrabold', sans-serif;
  font-size: 16px;
}
.content .articles p {
  font-size: 16px;
}
.content .articles a {
  display: block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'league_gothic_regular', sans-serif;
  color: black;
  float: right;
}
.content .articles a:hover {
  border-bottom: 2px solid #edbfff;
}
.content .logos {
  margin-top: 90px;
}
.content .logos ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
}
.content .logos ul li {
  width: 70px;
  height: 64px;
  margin-right: 25px;
  margin-left: 25px;
  margin-bottom: 25px;
  background-image: url(../../img/logos-sprite.png);
}
.content .logos ul li:nth-child(2) {
  background-position-x: -910px;
}
.content .logos ul li:nth-child(3) {
  background-position-x: -1042px;
}
.content .logos ul li:nth-child(4) {
  background-position-x: -1179px;
}
.content .logos ul li:nth-child(5) {
  background-position-x: -1300px;
  width: 110px;
  margin-right: 10px;
}
.content .logos ul li:last-child {
  background-position-x: -1472px;
  margin-left: 10px;
}
.categories {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 90px;
  padding-bottom: 90px;
  background-color: #ececec;
  border-top: 1px solid #d3d3d3;
}
footer {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 40px;
  background-image: url(../../img/footer-bg.jpg);
}
footer p {
  color: white;
  text-align: center;
}
footer .wrapper-footer {
  margin-bottom: 70px;
}
@media screen and (max-width: 767px) {
  .header-nav ul li:first-child {
    margin-left: auto;
    margin-right: auto;
  }
  .header-nav ul li:nth-child(n+2) {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  header .logo {
    float: left;
  }
  header .header-nav {
    clear: both;
  }
  header .header-nav a {
    font-size: 24px;
  }
  .about article h3 {
    margin-top: 0px;
  }
  .logos ul {
    justify-content: space-between;
  }
  .footer-nav a {
    font-size: 20px;
  }
  footer p {
    float: right;
    padding-right: 20px;
  }
}
@media screen and (min-width: 960px) {
  header .logo {
    display: inline-block;
  }
  header .header-nav {
    float: right;
    clear: right;
  }
  .about article:nth-child(2) {
    background-position-x: -335px;
  }
  .about article:last-child {
    background-position-x: -665px;
  }
  .footer-nav {
    float: left;
  }
}
@media screen and (min-width: 980px) {
  .centrator {
    width: 960px;
    margin-left: auto;
    margin-right: auto;
  }
  .wrapper-header,
  .wrapper-slider,
  .wrapper-about,
  .wrapper-content,
  .wrapper-categories,
  .wrapper-footer,
  .logos {
    width: 960px;
    margin-left: auto;
    margin-right: auto;
  }
}
